      <view class="product-grid">
        <view class="product-card" v-for="(item, index) in products" :key="index">
          <image :src="item.img" mode="aspectFit" class="product-img" />
          <view class="product-label">{{ item.label }}</view>
        </view>
      </view>
    </view>

