<script setup>
import { ref } from 'vue';
const selectedProvince = ref('四川');
// 你可以尝试增加或减少产品数量来测试效果
const products = ref([
  { img: 'https://via.placeholder.com/120x100?text=牦牛牛奶', label: '牦牛牛奶' },
  { img: 'https://via.placeholder.com/120x100?text=牦牛牛奶', label: '牦牛牛奶' },
  { img: 'https://via.placeholder.com/120x100?text=牦牛牛奶', label: '牦牛牛奶' },
  { img: 'https://via.placeholder.com/120x100?text=牦牛牛奶', label: '牦牛牛奶' },
]);

// 菜单、搜索、扫码、选择地区的事件
const openMenu = () => { uni.showToast({ title: '打开菜单', icon: 'none' }); };
const handleSearch = () => { uni.showToast({ title: '执行搜索', icon: 'none' }); };
const handleScan = () => { uni.navigateTo({ url: '/pages/scan/scan' }); };
const chooseLocation = () => {
  const provinces = ['北京', '天津', '河北', '山西', '内蒙古', '辽宁', '吉林', '黑龙江', '上海', '江苏', '浙江', '安徽', '福建', '江西', '山东', '河南', '湖北', '湖南', '广东', '广西', '海南', '重庆', '四川', '贵州', '云南', '西藏', '陕西', '甘肃', '青海', '宁夏', '新疆', '台湾', '香港', '澳门'];
  uni.showActionSheet({
    itemList: provinces,
    success: (res) => { selectedProvince.value = provinces[res.tapIndex]; }
  });
};
</script>

